@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@200&display=swap);
*{
  padding: 0;
  margin:0 ;
  box-sizing: border-box;
}


body {
  font-family: 'Public Sans', sans-serif;
  font-weight: 300;
  line-height: 1.6;
}
a {
  padding: 0;
  text-decoration: none;
}
li {
  list-style: none;
}

img {
  width: 100%;
}


.btn {
  border: none;
  padding: 10px 20px;
  outline: none;
  cursor: pointer;
  margin: 1rem;
  border-radius: 5px;
  display: inline-block;
  transition: all ease .2s;
  background: #007bff;
  color: #fff;
}

.btn-primary {
  padding: 8px 30px;
  border-radius: 100px;
}

.btn:hover {
  background: #007bff;
  color: #fff;
  box-shadow: 0 12px 4px rgba(0,0,0.0.4);
}

.wrapper {
  width: 75%;
  margin: auto;
}


.title {
  color: #FFFFFF;
  font-weight: bold;
  margin-bottom: 10px;
}

.title-lg {
  font-size: 84px;
  font-weight: bold;
}
.title-md {
  font-size: 40px;
  font-weight: bold;
}

.title-sm {
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: bold;
}

/* Media Quaries */
@media screen and (max-width:768px) {
   .wrapper {
    width: 90%;
  }
 
  .title-lg {
    font-size: 50px;
  }
  .title-md {
    font-size: 30px;
  }
}

